:root {
    --PrimaryColor: #F86F03;
        /*#4356FF */
        ;
    --SecondaryColor: hsl(37, 87.36%, 55.3%);
    --gradientColor: linear-gradient(to right, hsl(37, 87.36%, 55.3%), hsl(25, 98%, 50%));
    --whiteColor: hsl(0, 0%, 100%);
    --blackColor: hsl(201, 33%, 16%);
    --textColor: hsl(240, 4%, 36%);
    --whiteColorDeam: hsl(0, 0%, 93%);
    --greyText: rgb(190, 190, 190);
    --inputColor: rgb(239, 239, 239);
    --bodyColor: rgb(240, 240, 246);
    --cardBG: rgb(225, 225, 235);
    --cardHover:rgb(255,255,255);
}

.main{
    width: 100%;
    .secTitle{
        .title{
            position: relative;
            width: max-content;
            color: var(--textColor);
            margin: 1rem 0;  
            z-index: 2;
            &::after{
                position: absolute;
                content: '';
                background: var(--SecondaryColor);
                height: 5px;
                width: 120px;
                bottom: 5px;
                right: 0;
                z-index: -1;

            }
        }
    }
    .secContent{
        justify-content: center;
        gap:1rem;

        .singleDestination{
            height: 100%;
            display: grid;
            row-gap: 10px;
            border-radius: 10px;
            align-items: center;
            background:var(--cardBG);
            box-shadow: 0 2px 4px rgba(140,140,141,0.5);
            overflow: hidden;

            &:hover{
                background: var(--cardHover);
                box-shadow: 1 4px 4px rgba(85, 85, 114, 0.5);
                transition: .3s ease;
            }
            .imgDiv{
                height: 180px;
                width: 100%;
                overflow: hidden;
                img{
                    height: 100%;
                    width: 100%;
                    object-fit: cover;
                    transition: ease 2s;
                }
                &:hover{
                    img{
                        transform: scale(1.1);
                    }
                }
            }
            .cardInfo{
                padding: 1rem;

                .destTitle{
                    color:var(--blackColor);
                    font-size: 1.25rem;
                }
                .continent{
                    gap: .5rem;
                    .icon{
                        color: var(--textColor);
                        font-size: 18px;
                    }
                    .name{
                        color: var(--textColor);
                        font-size: 14px;
                        font-weight: 400;
                    }
                }
                .fees{
                    justify-content: space-between;
                    width: 100%;
                    padding: 1rem ;
                    margin: 1rem 0;
                    border-top: 1.5px solid var(--greyText);
                    border-bottom: 1.5px solid var(--greyText);

                    .grade{
                        color: var(--textColor);
                        max-width: 100px;
                        line-height: 20px;
                        display: flex;
                        align-items: center;
                        justify-content: center;

                        small{
                            text-align: center;
                            background:var(--gradientColor);
                            border-radius: 1rem;
                            color: var(--whiteColor);
                            padding: 1px 10px;
                            font-size: 10px;
                        }
                    }
                    .price{
                        font-size: 2rem;
                        color:var(--blackColor);
                    }
                }
                .desc{
                    font-size: 13.5px;
                    color: var(--textColor);
                    font-weight: 500;
                }
                .btn{
                    margin-top: 1rem;
                    color: var(--whiteColor);
                    justify-content: space-between;
                    font-weight: 600;
                    gap: .5rem;

                    .icon{
                        align-self: center;
                        font-size: 18px;
                    }
                }
            }
        }
    }
}

@media screen and (min-width:500px){
    .title{
        font-size: 1.5rem;
        &::after{
            width: 155px !important;
        }
    }
    .secContent{
        grid-template-columns: repeat(2,1fr);

        .singleDestination{
            height: auto;
        }
    }
}

@media screen and (min-width:840px) {
    .secContent {
            grid-template-columns: repeat(3, 1fr);
    
            .singleDestination {
                height: auto;
            }
        }
}